<template>
  <div>

    <v-dialog
        v-model="data.dialog"
        :persistent="data.loading"
        width="650"
    >

      <v-card :loading="data.loading" :disabled="data.loading" class="pass-form">

        <a-card-title>
          <v-img max-width="40" height="40" class="ml-2" :src="post.banner"/>
          <span style="font-size: 20px" v-if="post.title" >{{post.title}}</span>
          <span v-else >...</span>
          <v-spacer/>

          <span style="font-size: 16px" v-if="post.date_published_date" >{{post.date_published_date | momentTDate}}</span>
          <span v-else >...</span>
        </a-card-title>

        <v-card-text  style="min-height: 200px" class="pt-6">

          <div class="d-flex justify-space-between align-center" >
            <span style="font-size: 12px" class="mb-2"> وضعیت : <span v-if="post.enabled"
                                                                   style="color: darkgreen">فعال </span><span v-else-if="post.enabled===false"
                                                                                                                                                                                                          style="color: darkred">غیر فعال </span>
            </span>
          <span style="font-size: 12px" v-if="post.author && post.author.username" >نویسنده : {{post.author.username}}</span>
          <span v-else >...</span>
          </div>

          <div v-html="post.article" ></div>





        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>

        </v-card-actions>
        <template v-slot:progress >
          <GDLoading/>
        </template>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";

export default {
  name: "PostView",
  components: {ACardTitle, GDLoading},
  props : ["cdata"],
  data () {
    return {
      data : this.cdata,
      post : {},
    }
  },
  methods : {
    fetchPost() {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/post/post-detail/'+this.data.idSelected).then(response => {
        this.post = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.data.loading = false;
      })
    },
  },
  watch: {
    'data.dialog'(val) {
      this.data.loading = true;
      this.post = {};
      if (val) {
        this.fetchPost();
      }
    }
  }
}
</script>

